/*===================================
 * ハンバーガーメニュー
===================================*/
.header__hamburger {
  margin: 0;
  padding: 0;
  outline: none;
  border: none;
  position: relative;
  z-index: 999;
  width: 4rem;
  height: 3.75rem;
  border-radius: 10px;
  background-color: #00890E;
  cursor: pointer;
  transition: 0.3s;
}
@media screen and (min-width: 768px) {
  .header__hamburger {
    display: none;
  }
}

.header__hamburger.is-open {
  background-color: #fff;
  position: fixed;
  right: 1.25rem;
}

.header__hamburger span {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  display: block;
  width: 1.375rem;
  height: 1px;
  background-color: #fff;
  transition: 0.5s;
}

.header__hamburger.is-open span {
  width: 2rem;
  height: 3px;
  background-color: #00890E;
}

.header__hamburger span:nth-of-type(1) {
  top: -0.625rem;
}

.header__hamburger span:nth-of-type(2) {
  top: 0;
}

.header__hamburger span:nth-of-type(3) {
  top: 0.625rem;
}

.header__hamburger span:nth-of-type(3)::after {
  content: "メニュー";
  position: absolute;
  top: 0.1875rem;
  left: 50%;
  transform: translateX(-50%);
  color: #fff;
  font-size: 0.625rem;
  text-transform: uppercase;
  width: 3.75rem;
}

.header__hamburger.is-open span:nth-of-type(1) {
  top: 0;
  transform: translateX(-50%) rotate(35deg);
}

.header__hamburger.is-open span:nth-of-type(2) {
  opacity: 0;
}

.header__hamburger.is-open span:nth-of-type(3) {
  top: -0.375rem;
  transform: translateX(-50%) rotate(-35deg);
}

.header__hamburger.is-open span:nth-of-type(3)::after {
  content: "閉じる";
  transform: translateX(-50%) rotate(35deg);
  top: 0.625rem;
  left: 0.3125rem;
  width: 3.75rem;
  color: #00890E;
}

