@use '../foundation' as *;

.p-footer {
    padding: rem(45) 0;
    background-color: #D9D9D9;
    @include mq(md){
        padding: rem(30) 0 rem(20);
    }
}

.p-footer__inner {
    margin: auto;
    padding: 0 rem(15);
    width: 100%;
    @include mq(md){
        padding: 0 rem(25);
        max-width: rem(1250);
    }
}

.p-footer__wrap {
    @include mq(md){
        display: flex;
        justify-content: space-between;
    }
}

.p-footer__logo {
    margin: auto;
    max-width: rem(200);
    width: 100%;
    @include mq(md){
        margin: initial;
        max-width: rem(220);
    }
}

.p-footer__logo a {
    padding: rem(10) 0;
    display: block;
    @include mq(md){
        padding: rem(15) 0;
    }
}

.p-footer__logo img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.p-footer__nav-items {
    @include mq(md){
        display: flex;
        align-items: center;
    }
}

.p-footer__nav-item{
  display: flex;
  align-items: center;
  height:  rem(66);

  position: relative;
  &::before{
    content: "";
    position: absolute;
    top: 30%;
    right: 0;
    height: 24px;
    width: 1px;
    background-color: $color-green;
  }
  &:last-child::before {
    // 最後の要素の擬似要素のスタイルを無効にする
    content: none;
  }
}

.p-footer__nav-item a {
    padding: rem(12) 0;
    position: relative;
    display: block;
    font-size: rem(18);
    font-weight: 700;
    line-height: 1;
    text-align: center;
    @include mq(md){
        padding: rem(15);
    }
}

.p-footer__copyright {
    margin-top: rem(10);
    display: block;
    font-size: rem(12);
    line-height: 1;
    letter-spacing: 0.04em;
    text-align: center;
    @include mq(md){
        margin-top: rem(50);
    }
}

.p-footer__copyright span {
    text-transform: uppercase;
}
